<template>
  <div class="blogm-column">
    <van-sticky :offset-top="46">
      <van-tabs v-if="columnList" v-model="activeColumn" swipeable>
        <van-tab
          v-for="col in columnList"
          :key="col.id"
          :title="col.name"
          :name="col.id"
        />
      </van-tabs>
    </van-sticky>
    <ArticleList v-if="activeColumn" :columnId="activeColumn" />
  </div>
</template>

<script>
import ArticleList from '@/views/mobile/ArticleList.vue'
export default {
  name: 'VueBlogMColumn',
  components: { ArticleList },
  data() {
    return {
      columnList: null,
      activeColumn: ''
    }
  },
  created() {
    this.getColumnList()
  },
  mounted() {},

  methods: {
    //获取column
    async getColumnList() {
      try {
        const { list } = await this.$http({ type: 'getColumns' })
        this.columnList = list
        this.activeColumn = list[0].id
      } catch (err) {
        //console.log(err)
      }
    }
  }
}
</script>

