<template>
  <div class="blogm-user">
    <van-uploader
      :preview-image="false"
      :after-read="afterRead"
      use-before-read
      :before-read="beforeAvatarUpload"
      :disabled="!isLogin"
    >
      <van-image width="8rem" height="8rem" round fit="cover" :src="avatar">
      </van-image>
    </van-uploader>
    <h2 class="blogm-user-title">{{ title }}</h2>
    <BaseForm ref="form" :formType="formType" />
    <van-button type="info" block @click="submitForm">{{ btnName }}</van-button>
    <van-button
      v-if="formType === 'userInfo'"
      type="warning"
      block
      @click="$store.dispatch('logout')"
      >退出登录</van-button
    >
  </div>
</template>

<script>
import BaseForm from '@/components/mobile/base/BaseForm.vue'
import MODAL_CONFIG from '@/configs/modal.config'

import { mapActions, mapGetters } from 'vuex'
import { Toast } from 'vant'

export default {
  name: 'VueBlogMUser',
  components: { BaseForm },
  props: {
    state: {
      type: String,
      default: 'login'
    }
  },
  data() {
    return {
      isLogin: false,
      fileBase: require('../../assets/default_avatar.webp'),
      uploadedImg: ''
    }
  },
  created() {
    //验证登录状态 已登录的情况下 获取userInfo
    if (this.$store.state.token) {
      this.isLogin = true
      this.setUserInfo()
    }
  },
  computed: {
    ...mapGetters(['userInfo']),
    nickname() {
      return this.userInfo?.nickname
    },
    formType() {
      return this.nickname ? 'userInfo' : this.state
    },
    apiType() {
      return this.nickname ? 'putUserInfo' : this.state
    },
    uploadPath() {
      return process.env.VUE_APP_BASE_UPLOAD_URL + '/user'
    },
    // 上传后头像 -> 上传前头像 -> 未登录注册默认头像
    avatar() {
      return this.uploadedImg || this.userInfo?.avatar || this.fileBase
    },
    title() {
      return MODAL_CONFIG[this.formType]?.title ?? '默认标题'
    },
    //下方按钮名称
    btnName() {
      return MODAL_CONFIG[this.formType]?.btns[0]?.label ?? '提交'
    }
  },

  methods: {
    ...mapActions(['setUserInfo']),
    async submitForm() {
      const refForm = this.$refs['form']
      const vanForm = refForm.$refs['vanForm']
      try {
        await vanForm.validate()/
        await this.$http({
          type: this.apiType,
          data: refForm.form
        })
        this.setUserInfo()
        Toast.success('修改成功')
      } catch (err) {
        Toast({ message: '请检查您的输入内容', position: 'bottom' })
        //重置
        refForm.form = {}
        vanForm.resetValidation()
      }
    },
    //读取完上传
    async afterRead({ file }) {
      let formData = new FormData()
      formData.append('user', file)
      try {
        let { fileURL } = await this.$http({
          type: 'uploadAvatar',
          data: formData
        })
        //上传成功 同时更改form 和 el-image
        this.$bus.$emit('change-avatar', fileURL)
        this.uploadedImg = fileURL
      } catch (err) {
        Toast.fail({ message: `上传失败` })
      }
    },
    //上传前
    beforeAvatarUpload(file) {
      let errMsg
      let isImg = /^image\/(jpeg|png|gif|bmp)$/.test(file.type)
      let isSize = file.size < 5 * 1024 * 1024

      if (!isImg) {
        errMsg = '上传头像图片只能是 JPG/PNG/GIF/BMP 格式!'
      }
      if (!isSize) {
        errMsg = '上传头像图片大小不能超过5M !'
      }

      if (!isImg || !isSize) {
        Toast.fail({ message: `上传错误,${errMsg}` })
      }

      return isImg && isSize
    }
  }
}
</script>

<style lang="stylus">
.blogm-user
  display flex
  flex-direction column
  align-items center
  justify-content center
  padding 0 20px
  .van-button
    width 88%
    margin-top 10px
  .blogm-user-title
    font-size 24px
    line-height 60px
    font-weight 600
    color #323233
  .blogm-form
    width 100%
    box-shadow 0 0 5px #eee
    background-color #fff
    border-radius 6px

// hack 透明度
.van-uploader__wrapper.van-uploader__wrapper--disabled
  opacity 1
</style>