<template>
  <div class="blogm-article--comment">
    <h2 class="blogm-comment--head">评论列表</h2>
    <van-button
      round
      block
      type="info"
      class="blogm-comment--btn"
      @click="showArtComment"
      >写评论</van-button
    >
    <div v-if="comments?.length" class="blogm-comment--list">
      <van-cell-group
        v-for="(comment, idx) in comments"
        :key="comment.id"
        class="blogm-comments--item"
      >
        <van-cell
          class="blogm-comment--top"
          :title="idx + 1 + '#  ' + comment.uid.nickname"
          :value="comment.date"
        />
        <van-cell :title="comment.content" />
      </van-cell-group>
    </div>
    <van-empty v-else description="还未有评论哦" />
  </div>
</template>

<script>
export default {
  name: 'VueBlogMArticleComment',
  props: {
    comments: {
      type: Array
    }
  },
  computed: {
    hasComment() {
      return this?.comments?.length
    }
  },
  methods: {
    showArtComment() {
      this.$bus.$emit('show-art-comment')
    }
  }
}
</script>

<style lang="stylus">
.blogm-comment--head
  font-size 1.5em
  padding 1em 0
  text-align center

.blogm-comments--item
  margin-bottom 10px
  box-shadow 0 1px 4px #ccc

.van-button.blogm-comment--btn
  width 70%
  margin 10px auto 20px

.blogm-comment--list
  padding 0 10px

.van-cell.blogm-comment--top
  background-color theme-color-skyblue
  border-radius 6px 6px 0 0
  *
    color #fff
  &::after
    border 0
</style>