<template>
  <div class="blogm-article--wrap">
    <h2 class="blogm-article--title">{{ article.title }}</h2>
    <ArticleInfoBar v-if="info" :info="info" />
    <van-divider />
    <!-- 使用v-html 前加上v-once保证安全性 -->
    <div
      class="blogm-article--content markdown-body"
      v-if="article.content"
      v-once
      v-html="article.content"
    />
  </div>
</template>

<script>
// import { createNamespacedHelpers } from 'vuex'
// const { mapActions } = createNamespacedHelpers('artLikes')
import ArticleInfoBar from '@/components/mobile/article/ArticleInfoBar.vue'

export default {
  name: 'VueBlogMArticleContent',
  components: { ArticleInfoBar },
  props: {
    article: {
      type: Object,
      default() {
        return {
          title: '默认标题',
          author: { nickname: '佚名' }
        }
      }
    }
  },
  computed: {
    nickname() {
      return this.article.author?.nickname
    },
    avatar() {
      return this.article.author?.avatar
    },
    info() {
      const { date, like_num, hit_num, comment_num } = this.article
      return {
        date,
        like_num,
        hit_num,
        comment_num,
        nickname: this.nickname,
        avatar: this.avatar
      }
    }
  }
}
</script>

<style lang="stylus">
.blogm-article--wrap
  padding 15px
  .blogm-article--content.markdown-body
    background-color theme-bgcolor-light
  .blogm-article--title
    line-height 50px
    text-align center
    font-size 24px
    border-radius 4px
    color theme-bgcolor-light
    background-color theme-color-orange
  .blog-article--info
    padding: padding-per * 2 0
    border-radius bd-radius-per
    border 2px dashed #ccc
    justify-content space-around
    align-items center
  .van-divider
    border-color hsla(210, 18%, 87%, 1)
    padding: padding-per * 2 0
</style>