<template>
  <div class="blogm-article">
    <ArticleContent v-if="article" :article="article" />
    <ArticleComment v-if="article" :comments="article.comments" />
    <!-- 评论编辑器 -->
    <van-action-sheet :round="false" v-model="showCommentEditor" title="评论">
      <van-field
        v-model="commentText"
        rows="2"
        autosize
        type="textarea"
        maxlength="120"
        placeholder="看帖是喜欢，评论才是真爱"
        show-word-limit
        class="blogm-comment--editor"
      />
      <van-button
        type="info"
        block
        class="blogm-comment--submit"
        @click="submitComment"
        >提 交</van-button
      >
    </van-action-sheet>
    <!-- 分享面板 -->
    <van-share-sheet
      @select="onShareSelect"
      v-model="showShare"
      :options="options"
      title="立即分享给好友"
    />
  </div>
</template>

<script>
import ArticleContent from '@/components/mobile/article/ArticleContent.vue'
import ArticleComment from '@/components/mobile/article/ArticleComment.vue'

import { Toast } from 'vant'
export default {
  name: 'VueBlogMArticle',
  components: { ArticleContent, ArticleComment },
  props: {
    id: {
      type: String
    }
  },
  data() {
    return {
      article: null,
      showCommentEditor: false,
      commentText: '',
      showShare: false,
      options: [
        { name: '微信', icon: 'wechat' },
        { name: '微博', icon: 'weibo' },
        { name: '复制链接', icon: 'link' },
        { name: '分享海报', icon: 'poster' },
        { name: '二维码', icon: 'qrcode' }
      ]
    }
  },
  created() {
    this.getArticleById(this.id)
  },
  mounted() {
    this.$bus.$on('show-art-share', this.showArtShare)
    this.$bus.$on('show-art-comment', this.showArtComment)
  },
  beforeDestroy() {
    this.$bus.$off('show-art-share', this.showArtShare)
    this.$bus.$off('show-art-comment', this.showArtComment)
  },

  methods: {
    onShareSelect(option) {
      if (option.name === '复制链接') {
        this.copyLink()
      }
    },
    //使用 vue-clipboard2插件复制链接
    async copyLink() {
      try {
        await this.$copyText(window.location.href)
        Toast('复制链接成功')
      } catch (error) {
        Toast('复制链接失败')
      }
    },
    showArtShare() {
      this.showShare = true
    },
    showArtComment() {
      //验证是否为登录用户
      if (!this.$store.state.token) {
        this.$bus.$emit('show-dialog', 'login')
        Toast({ message: '请先登录', position: top })
        return false
      }
      this.showCommentEditor = true
    },
    async submitComment() {
      if (!this.commentText.trim().length) {
        //评论是否为空
        Toast.fail({ forbidClick: true, message: '请输入评论内容' })
        return false
      }
      try {
        await this.$http({
          type: 'postComment',
          data: { content: this.commentText, aid: this.id }
        })

        //提交完评论后刷新列表
        this.getArticleById(this.id)
        this.commentText = ''
        this.showCommentEditor = false

        Toast.success({ forbidClick: true, message: '评论成功' })
      } catch (err) {
        Toast.fail({ forbidClick: true, message: '评论失败' })
      }
    },
    async getArticleById(id) {
      try {
        let article = await this.$http({
          type: 'getArticleById',
          data: { id }
        })
        this.article = article
      } catch (err) {
        //console.log(err)
      }
    }
  }
}
</script>

<style lang="stylus">
.blogm-article
  min-height 100%

.blogm-comment--editor
  min-height 100px
  .van-cell__value
    padding 10px
    background-color #f1f1f1
    border-radius 8px

.van-button.blogm-comment--submit
  width 90%
  margin 10px auto
</style>