<template>
  <van-card class="blogm-article--info">
    <template #thumb>
      <van-image
        class="blogm-info--avatar"
        round
        width="80"
        height="80"
        :src="info.avatar"
      />
    </template>
    <template #title>
      <h3 class="blogm-info--author">{{ info.nickname }}</h3>
    </template>
    <template #tags>
      <div class="blom-info--icons">
        <van-icon name="eye" /><span>{{ info.hit_num }}</span>
        <van-icon name="like" /><span>{{ info.like_num }}</span>
        <van-icon name="comment" /><span>{{ info.comment_num }}</span>
      </div>
    </template>
    <template #num>
      <h3 class="blogm-info--date">发布于: {{ info.date }}</h3>
    </template>
  </van-card>
</template>

<script>
import MIXIN_ART_LIKES from '@/core/mixin_artLikes'
export default {
  name: 'VueBlogArticleInfoBar',
  mixins: [MIXIN_ART_LIKES],
  props: {
    info: {
      type: Object
    }
  },
  data() {
    return {}
  },
  computed: {
    like_num() {
      return Math.max(this.info.like_num + this.step_num, 0)
    }
  },
  methods: {}
}
</script>

<style lang="stylus">
.blogm-article--info.van-card
  background-color rgba(220, 220, 220, 0.18)
  border-radius 6px
  .van-card__header
    height 80px

.blogm-info--author
  font-size 16px
  font-weight 600

.blom-info--icons
  display flex
  font-size 16px
  align-items center
  justify-content flex-end
  color #999
  *
    margin-left 5px
</style>